import styled from "styled-components";
import { media } from "../../constants/styles";

const Styles = styled.main`
  h1 {
    ${media.pc`
      font-size: 2.5rem;
    `};
    ${[media.tab, media.sp].map(
      m => `
      font-size: 1.5rem;
    `
    )};
    }
  }

  .postInfo {
    background: #f3f3f3;
    padding: 1rem;
    ${media.pc`
      margin-bottom: 3rem;
    `}
    ${[media.tab, media.sp].map(
      m => m`
      margin-bottom: 1rem;
    `
    )}
  }

  .socialButtons {
    display: flex;
    ${media.pc`
      display: none;
    `};

    div,
    iframe {
      margin-right: 0.5rem;
    }
  }

  .postContent {
    ${media.pc`
      h2 {
        font-size: 1.8rem;
        margin-top: 4rem;
      }
      h3 {
        font-size: 1.6rem;
        margin-top: 3rem;
      }
      h4 {
        font-size: 1.4rem;
        margin-top: 2rem;
      }
      h5 {
        font-size: 1.3rem;
      }
    `}
    ${[media.tab, media.sp].map(
      m => m`
      h2 {
        font-size: 1.3rem;
        margin-top: 3rem;
      }
      h3 {
        font-size: 1.2rem;
        margin-top: 2rem;
      }
      h4 {
        font-size: 1.1rem;
        margin-top: 1.5rem;
      }
      h5 {
        font-size: 1rem;
      }
    `
    )}

    margin-bottom: 5rem;

    ul.tags {
      margin: 0.5rem 0;
    }

    h2 {
      padding-bottom: 0.5rem;
      border-bottom: 1px #ccc solid;
    }

    blockquote {
      font-size: 1.3rem;
      font-style: italic;
      color: #aaa;
      border-top: 1px #ccc solid;
      border-bottom: 1px #ccc solid;
      width: 95%;
      margin: 2.5rem 0;
    }

    pre {
      margin: 2rem 0;
      ${[media.tab, media.sp].map(
        m => m`
        width: 95vw;
        overflow: scroll;
      `
      )}
      div.file {
        display: flex;

        div.text {
          padding: 0.3rem 0.6rem;
          font-size: 0.8rem;
          background: #555;
          border-radius: 0.2rem 0.2rem 0 0;
          color: #ffffff;
        }
      }

      code {
        padding: 1.2rem;
      }
    }

    p > code {
      background: #eaeaea;
      padding: 0 0.4rem;
      border-radius: 0.1rem;
    }

    table {
      border-collapse: collapse;
      ${[media.tab, media.sp].map(
        m => m`
        table-layout: fixed;
        width: 95vw;
      `
      )}

      th, td {
        overflow: auto;
      }

      th[style="text-align:right"] {
        display: none;
      }

      td[style="text-align:right"] {
        text-align: left !important;
      }

      th {
        background: #555;
        color: #fff;
        border: 1px #ccc solid;
        padding: 1rem 0.8rem;
      }

      td {
        border: 1px #ccc solid;
        padding: 0.8rem;
      }
    }

    ul,
    ol {
      font-size: 1.1rem;

      li {
        margin-bottom: 0.3rem;
      }
    }

    hr {
      margin: 3rem 0;
    }

    p {
      font-size: 1.1rem;
    }

    img {
      margin: 2rem 0;
      box-shadow: 0px 5px 15px -5px rgba(0, 0, 0, 0.8);
    }

    a {
      word-break: break-all;
    }

    .cstmreba {
      margin: 2rem 0;

      .kaerebalink-box {
        border: 1px #ccc solid;
        background: #ececec;
        padding: 1.5rem;
        display: flex;
        justify-content: center;
        align-items: center;

        .kaerebalink-info {
          margin-left: 2rem;

          .kaerebalink-name > a {
            font-size: 1.2rem;
          }
        }

        img {
          margin: 0;
        }
      }
    }

    twitter-widget {
      width: 95vw !important;
    }
  }
`;

export default Styles;



