import { css } from "styled-components";

export const widths = {
  tablet: "750px",
  sp: "360px",
  mainContent: "700px"
};

export const media = {
  pc: (s, ...args) => css`
    @media (min-width: ${widths.tablet}) {
      ${css(s, ...args)};
    }
  `,
  tab: (s, ...args) => css`
    @media (max-width: ${widths.tablet}) {
      ${css(s, ...args)};
    }
  `,
  sp: (s, ...args) => css`
    @media (max-width: ${widths.sp}) {
      ${css(s, ...args)};
    }
  `
};

export default {
  color: {
    accent: "#2199e8",
    accentHover: "#008cba"
  },
  border: "1px #aaa solid"
};



