import React from "react";
import { Link } from "react-static";
import styled from "styled-components";
import styles, { media } from "../constants/styles";

const Styles = styled.div`
  header {
    width: 100%;
    background: #333333;
    color: #fff;
    border-bottom: ${styles.border};
    ${media.pc`
      margin-bottom: 1rem;
    `} text-align: center;
    cursor: pointer;

    a {
      &:visited {
        color: #ffffff;
      }
    }

    h1 {
      font-weight: normal;
      margin-bottom: 0.3rem;
      margin-top: 0;
      ${media.pc`
        font-size: 2.5rem;
        padding-top: 1rem;
      `};
      ${[media.tab, media.sp].map(
        m => m`
        font-size: 1.8rem;
        padding-top: 0.5rem;
      `
      )};
    }

    p {
      margin-top: 0;
      ${media.pc`
        margin-bottom: 1rem;
      `};
      ${[media.tab, media.sp].map(
        m => m`
        font-size: 0.8rem;
        margin-bottom: 0.5rem;
      `
      )};
    }
  }
`;

export default () => (
  <Styles>
    <header>
      <Link to="/">
        <h1>BAKUNOTE</h1>
        <p>bakunyoの技術ノート</p>
      </Link>
    </header>
  </Styles>
);



