import React from "react";
import { getRouteProps, Link } from "react-static";
import { Helmet } from "react-helmet";
import FaCalendar from "react-icons/lib/fa/calendar";
import FaTags from "react-icons/lib/fa/tags";
import Hatebu from "../../parts/hatebu";
import Twitter from "../../parts/twitter";
import Pocket from "../../parts/pocket";
import Styles from "./styles";
import LeftBar from "../LeftBar";
import RightBar from "../RightBar";
import blog from "../../constants/blog";
import withTracker from "../../parts/withTracker";

class Post extends React.Component {
  componentWillMount() {
    if (typeof window === "undefined") {
      global.window = {};
    }

    window.scrollTo && window.scrollTo(0, 0);
  }

  componentDidMount() {
    [].forEach.call(document.getElementsByClassName("speakerdeck"), tag => {
      const script = document.createElement("script");
      script.src = "//speakerdeck.com/assets/embed.js";
      script.className = "speakerdeck-embed";
      script.dataset.id = tag.dataset.id;
      script.dataset.ratio = "1.33333333333333";
      tag.appendChild(script);
    });

    // tweet 埋め込みの blockquote 部分だけ md に書き込めば発動する
    if (document.getElementsByClassName("twitter-tweet").length > 0) {
      const script = document.createElement("script");
      script.src = "//platform.twitter.com/widgets.js";
      script.async = true
      script.charset = "utf-8"
      document.body.appendChild(script);
    }

    // target blankにする
    const links = document.links;
    for (var i = 0, linksLength = links.length; i < linksLength; i++) {
      if (links[i].hostname != window.location.hostname) {
        links[i].target = "_blank";
      }
    }
  }

  render() {
    const { post } = this.props;
    const tagComment =
      post.tags.length > 0 ? `${post.tags.join(",")}に関する記事。` : "";

    return (
      <div className="content">
        <LeftBar post={post} />
        <Styles>
          <Helmet>
            <title>{post.title}</title>
            <link
              rel="canonical"
              href={`${blog.protocol}${blog.domain}${post.path}`}
            />
            <meta
              name="description"
              content={`${post.description || post.title} ${tagComment}${
                blog.description
              }`}
            />
          </Helmet>
          <h1>{post.title}</h1>
          <div className="postInfo">
            <div className="postMeta">
              <div className="meta">
                <FaCalendar />
                <span>{post.date}</span>
              </div>
              <div className="meta">
                <FaTags />
                <ul className="tags">
                  {post.tags.map(tag => (
                    <Link key={`${post.id}-${tag}`} to={`/tags/${tag}/`}>
                      <li>{tag}</li>
                    </Link>
                  ))}
                </ul>
              </div>
            </div>
          </div>
          <div className="socialButtons">
            <Twitter post={post} />
            <Hatebu post={post} />
            <Pocket />
          </div>
          <div
            className="postContent"
            dangerouslySetInnerHTML={{ __html: post.body }}
          />
          <div className="socialButtons">
            <Twitter post={post} />
            <Hatebu post={post} />
            <Pocket />
          </div>
        </Styles>
        <RightBar />
      </div>
    );
  }
}

export default getRouteProps(withTracker(Post));



