import React from "react";
import ReactPaginate from "react-paginate";
import styled from "styled-components";
import styles from "../constants/styles";

const Styles = styled.div`
  ul {
    display: flex;
    list-style: none;
    justify-content: center;
    padding: 0;
    margin: 3rem 0 2rem 0;

    li {
      font-size: 1rem;
      margin-left: 0.4rem;
      cursor: pointer;

      a {
        border: 1px #ccc solid;
        margin: 0;
        padding: 0.6rem 0.8rem;

        &:visited {
          color: #000;
        }
      }

      &.active > a {
        background: ${styles.color.accent};
        color: #fff;
      }
    }
  }
`;

const hrefBuilder = (path, page) => {
  const withoutPagePath = path.replace(/p\d+\/$/, "");
  return page === 1 ? withoutPagePath : `${withoutPagePath}p${page}/`;
};

export default ({ page, onPageChange, path }) => (
  <Styles>
    <ReactPaginate
      previousLabel={"<"}
      nextLabel={">"}
      pageRangeDisplayed={2}
      marginPagesDisplayed={0}
      breakLabel={null}
      pageCount={parseInt(page.all)}
      initialPage={parseInt(page.now) - 1}
      forcePage={parseInt(page.now) - 1}
      activeClassName={"active"}
      onPageChange={onPageChange}
      disableInitialCallback={true}
      hrefBuilder={p => hrefBuilder(path, p)}
    />
  </Styles>
);



