import React from "react";
import blog from "../constants/blog";

export function isNewerHatebu(post) {
  return new Date(post.date) > new Date(2016, 6);
}

export function isNewerHatebuEntry(post) {
  return new Date(post.date) > new Date(2017, 8);
}

export function hatebuPath(post) {
  return isNewerHatebu(post) ? post.path : post.path.slice(0, -1);
}

export default class Hatebu extends React.Component {
  componentDidMount() {
    const button = this.node;
    const script = document.createElement("script");
    script.src = "https://b.st-hatena.com/js/bookmark_button.js";
    script.type = "text/javascript";
    script.charset = "utf-8";

    button.parentNode.appendChild(script);
  }

  render() {
    const { post } = this.props;
    const path = hatebuPath(post);

    return (
      <a
        ref={node => (this.node = node)}
        href={`http${isNewerHatebuEntry(post) ? "s" : ""}://b.hatena.ne.jp/entry/${blog.domain}${path}`}
        className="hatena-bookmark-button"
        data-hatena-bookmark-layout="vertical-large"
        data-hatena-bookmark-lang="ja"
        title="このエントリーをはてなブックマークに追加"
      >
        <img
          src="https://b.st-hatena.com/images/entry-button/button-only@2x.png"
          alt="このエントリーをはてなブックマークに追加"
          width="20"
          height="20"
          style={{ border: "none" }}
        />
      </a>
    );
  }
}



