import React from "react";
import { getRouteProps, Link } from "react-static";
import styled from "styled-components";
import { Helmet } from "react-helmet";
import FaCalendar from "react-icons/lib/fa/calendar";
import FaTags from "react-icons/lib/fa/tags";
import styles, { media } from "../constants/styles";
import blog from "../constants/blog";
import Pagination from "./Pagination";
import { hatebuPath, isNewerHatebuEntry } from "../parts/hatebu";
import withTracker from "../parts/withTracker";

const Styles = styled.main`
  .posts {
    margin-bottom: 2rem;

    a {
      color: inherit;
    }
    a:visited {
      color: inherit;
    }

    article {
      border: ${styles.border};
      padding: 1rem;
      margin-bottom: 1rem;
      cursor: pointer;

      h2 {
        margin: 0.5rem 0;
        ${media.pc`
          font-size: 1.5rem;
        `};
        ${[media.tab, media.sp].map(
          m => m`
          font-size: 1.3rem;
        `
        )};
        font-weight: normal;
      }

      &:hover bold {
        background: #eee;
        transition: all 0.2s ease-in-out;
      }

      ul.tags {
        margin: 0;
      }

      img.hatebuImg {
        vertical-align: middle;
        margin-left: 0.5rem;
      }
    }
  }
`;

class PostList extends React.Component {
  constructor({ props }) {
    super(props);
    this.onClickPage = this.onClickPage.bind(this);
    this.onTagClicked = this.onTagClicked.bind(this);
  }

  componentWillMount() {
    if (typeof window === "undefined") {
      global.window = {};
    }

    window.scrollTo && window.scrollTo(0, 0);
  }

  onClickPage(data) {
    const newPage = data.selected + 1;
    const path = location.pathname;
    const regex = /\/p\d+\/$/;
    let newPath;

    if (newPage === 1) {
      newPath = path.replace(regex, "/");
    } else {
      newPath = path.match(regex)
        ? path.replace(regex, `/p${newPage}/`)
        : `${path}p${newPage}/`;
    }

    this.props.history.push(newPath);
    window.scrollTo(0, 0);
  }

  onTagClicked(e) {
    e.preventDefault();
    const href = e.target.dataset.href;
    this.props.history.push(href);
    window.scrollTo(0, 0);
  }

  render() {
    const { posts, title, page, path } = this.props;
    const href = `${blog.protocol}${blog.domain}${path}`;

    return (
      <div className="content">
        <Styles>
          <Helmet>
            <title>{title}</title>
            <link rel="canonical" href={href} />
            <meta
              name="description"
              content={`${title || "記事一覧"} ${page.now}ページ目。${
                blog.description
              }`}
            />
          </Helmet>
          {title === "" ? "" : <h1>{title}</h1>}
          <div className="posts">
            {posts.map(post => (
              <Link key={`${post.id}`} to={post.path}>
                <article>
                  <h2>{post.title}</h2>
                  <div className="postInfo">
                    <div className="postMeta">
                      <div className="meta">
                        <FaCalendar />
                        <span>{post.date}</span>
                        <img
                          className="hatebuImg"
                          src={`https://b.hatena.ne.jp/entry/image/http${isNewerHatebuEntry(post) ? "s" : ""}://${
                            blog.domain
                          }${hatebuPath(post)}`}
                        />
                      </div>
                      <div className="meta">
                        <FaTags />
                        <ul className="tags">
                          {post.tags.map(tag => (
                            <li
                              key={`${post.id}-${tag}`}
                              data-href={`/tags/${tag}/`}
                              onClick={this.onTagClicked}
                            >
                              {tag}
                            </li>
                          ))}
                        </ul>
                      </div>
                    </div>
                  </div>
                </article>
              </Link>
            ))}
          </div>
          {parseInt(page.all) <= 1 ? (
            ""
          ) : (
            <Pagination
              page={page}
              onPageChange={this.onClickPage}
              path={path}
            />
          )}
        </Styles>
      </div>
    );
  }
}

export default getRouteProps(withTracker(PostList));



