import React from "react";
import Styles from "./styles";
import Twitter from "../../parts/twitter";
import Hatebu from "../../parts/hatebu";
import Pocket from "../../parts/pocket";

export default ({ post }) => (
  <Styles>
    <div className="socialLinks">
      <Twitter post={post} />
      <Hatebu post={post} />
      <Pocket />
    </div>
  </Styles>
);



