import React from "react";
import blog from "../constants/blog";
import styled from "styled-components";
import FaTwitter from "react-icons/lib/fa/twitter";

const Styles = styled.div`
  a.button {
    display: block;
    padding: 0.5rem 1rem;
    color: #fefefe;
    background: #1583cc;
    width: 3rem;
    text-align: center;
    border-radius: 0.2rem;
    font-size: 0.9rem;

    svg {
      font-size: 1.5rem;
    }
  }
`;

export default class Twitter extends React.Component {
  render() {
    const { post } = this.props;
    const url = `${blog.protocol}${blog.domain}${post.path}&url=${url}`;
    const href = `https://twitter.com/intent/tweet?text=${post.title} - ${
      blog.title
    } ${url}`;

    return (
      <Styles>
        <a
          className="button"
          href="#"
          onClick={() => {
            window.open(
              encodeURI(decodeURI(href)),
              "tweetwindow",
              "width=650, height=470, personalbar=0, toolbar=0, scrollbars=1, sizable=1"
            );
            return false;
          }}
        >
          <FaTwitter />
          Tweet
        </a>
      </Styles>
    );
  }
}



