import React from "react";
import { getSiteProps, Link } from "react-static";
import styled from "styled-components";
import FaTwitter from "react-icons/lib/fa/twitter";
import bakunyoImg from "../images/bakunyo.png";
import { media } from "../constants/styles";

const Styles = styled.div`
  footer {
    margin-top: 1rem;
    padding: 1rem;
    background: #eee;
    text-align: center;

    nav {
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      margin-bottom: 2rem;
      section {
        text-align: left;
        margin: 0 2rem 1rem 0;
        flex: 1;

        &.profile {
          div.self {
            display: flex;
            margin-bottom: 1rem;

            svg {
              height: 1.5rem;
              width: 1.5rem;
            }

            img {
              width: 6rem;
              height: 6rem;
              border-radius: 3rem;
            }

            p {
              margin: 0 0 0 1rem;
            }
          }
        }

        &.links {
          ul.tags {
            margin: 0.6rem 0;

            li {
              margin-left: 0.2rem;
            }
          }
        }
      }
      ${[media.tab, media.sp].map(
        m => m`
        flex-direction: column;
      `
      )};
    }
    .footerSmalls {
      span,a {
        margin: 0 0.2rem;
      }
    }
  }
`;

class Footer extends React.Component {
  constructor({ props }) {
    super(props);
    this.onTagClicked = this.onTagClicked.bind(this);
  }

  onTagClicked(e) {
    window.scrollTo(0, 0);
  }

  render() {
    const { tagNames } = this.props;

    return (
      <Styles>
        <footer>
          <nav>
            <section className="profile">
              <h3>Profile</h3>
              <div className="self">
                <img src={bakunyoImg} alt="bakunyo" />
                <p>
                  <FaTwitter />&nbsp;
                  <a href="https://twitter.com/bakunyo">@bakunyo</a>
                  <br />
                  <br />
                  <span>
                    フリーランスのWEBエンジニアで普段は Rails / React /
                    TypeScript あたりを書いてます。
                    お仕事の話あれば気軽に連絡ください。
                    <a target="_blank" href="https://xn--0st94o12a.jp/">
                      最新巻.jp
                    </a>{" "}
                    運営中。亀戸に在住。
                  </span>
                </p>
              </div>
              <div className="feedly">
                <a
                  href="https://feedly.com/i/subscription/feed/https://blog.bakunyo.com/rss"
                  target="blank"
                >
                  <img
                    id="feedlyFollow"
                    src="https://s3.feedly.com/img/follows/feedly-follow-rectangle-flat-big_2x.png"
                    alt="follow us in feedly"
                    width="131"
                    height="56"
                  />
                </a>
              </div>
            </section>
            <section className="links">
              <h3>Tags</h3>
              <ul className="tags">
                {tagNames.map(tag => (
                  <Link
                    key={`footer-${tag}`}
                    to={`/tags/${tag}/`}
                    onClick={this.onTagClicked}
                  >
                    <li>{tag}</li>
                  </Link>
                ))}
              </ul>
            </section>
          </nav>
          <small className="footerSmalls">
            <a href="https://twitter.com/bakunyo">@bakunyo</a>
            <span>&copy; 2013 - {new Date().getFullYear()}&nbsp;</span>
            <a href="/privacy">プライバシーポリシー</a>
          </small>
        </footer>
      </Styles>
    );
  }
}

export default getSiteProps(Footer);



