import styled from "styled-components";
import { media } from "../../constants/styles";

export default styled.div`
  margin-top: 2.5rem;
  width: 8rem;
  position: relative;

  ${[media.tab, media.sp].map(
    m => m`
    display: none;
  `
  )};

  .socialLinks {
    position: sticky;
    top: 2rem;

    div,
    iframe {
      margin-bottom: 0.5rem;
    }
  }
`;



