import styled, { injectGlobal } from "styled-components";
import styles, { media, widths } from "../constants/styles";

injectGlobal`
  body {
    font-family: -apple-system, Hiragino Kaku Gothic ProN, BlinkMacSystemFont, "Helvetica Neue", "Segoe UI","Noto Sans Japanese","ヒラギノ角ゴ ProN W3", Meiryo, sans-serif;
    font-weight: 400;
    font-size: 18px;
    margin: 0;
    padding: 0;
    color: #222;
  }
`;

export default styled.div`
  a {
    text-decoration: none;
  }

  .content {
    display: flex;
    justify-content: center;
    position: relative;
  }

  ${media.pc`
    main,
    nav {
      width: ${widths.mainContent};
    }
  `};

  main {
    padding: 0 0.5rem;
  }

  img {
    max-width: 100%;
  }

  .postInfo {
    .postMeta {
      margin-right: 3rem;
      p {
        margin: 0.5rem 0;
      }
    }
  }

  .meta {
    color: #555;
    display: flex;
    align-items: center;
    svg {
      margin-right: 0.5rem;
    }
    ul {
      margin: 0;
    }
  }

  ul.tags {
    display: flex;
    list-style: none;
    padding-left: 0;
    flex-wrap: wrap;

    li {
      font-size: 0.9rem;
      background: ${styles.color.accent};
      margin: 0.2rem;
      color: #ffffff;
      padding: 0.2rem 0.5rem;
      border-radius: 0.1rem;
      cursor: pointer;

      &:first-child {
        margin-left: 0;
      }

      &:hover {
        background: ${styles.color.accentHover};
        transition: all 0.2s ease-in-out;
      }
    }
  }
`;



